// src/llm/langchain/npcSystem.ts
interface NPC {
  id: number;
  name: string;
  age: number;
  gender: string;
  occupation: string;
  personality: string;
  likes: string[];
  dislikes: string[];
}

class SimpleMemory {
  private messages: Array<{ type: 'human' | 'ai', content: string }> = [];
  private maxMessages: number;
  
  constructor(maxMessages: number = 5) {
    this.maxMessages = maxMessages;
  }
  
  getMessages(): string {
    // 
    if (this.messages.length > this.maxMessages) {
      this.messages = this.messages.slice(-this.maxMessages);
    }
    
    return this.messages.map(msg => 
      `${msg.type === 'human' ? '' : 'NPC'}: ${msg.content}`
    ).join('\n');
  }
  
  addMessage(input: string, response: string) {
    this.messages.push({ type: 'human', content: input });
    this.messages.push({ type: 'ai', content: response });
  }
}

class NPCSystem {
  private npcs: NPC[];
  private memory: Map<number, SimpleMemory>;

  constructor() {
    this.npcs = [
      {
        id: 1,
        name: "",
        age: 45,
        gender: "",
        occupation: "",
        personality: "",
        likes: ["", "", ""],
        dislikes: ["", ""],
      },
      {
        id: 2,
        name: "",
        age: 20,
        gender: "",
        occupation: "",
        personality: "",
        likes: ["", "", ""],
        dislikes: ["", ""],
      },
      {
        id: 3,
        name:"",
        age:22,
        gender:"",
        occupation:"",
        personality:"",
        likes:["","",""],
        dislikes:["",""]
      },
      {
        id:4,
        name:"HR",
        age:30,
        gender:"",
        occupation:"HR",
        personality:"",
        likes:["","",""],
        dislikes:["",""]
      }
    ];
    this.memory = new Map();
  }

  getNPC(npcId: number): NPC | undefined {
    return this.npcs.find(npc => npc.id === npcId);
  }

  getMemory(npcId: number): SimpleMemory {
    if (!this.memory.has(npcId)) {
      this.memory.set(npcId, new SimpleMemory(5));
    }
    return this.memory.get(npcId)!;
  }

  // NPC
  addNPC(npc: NPC) {
    this.npcs.push(npc);
  }
}

export { NPCSystem, SimpleMemory };
export type { NPC };